.skill-cart {
    display: grid;
    grid-template-columns: repeat(2, 1fr);

    @include respond(tab-port) {
        grid-template-columns: min-content 1fr;
    }

    column-gap: 2rem;
    cursor: pointer;

    .circle {
        justify-self: center;
        align-self: center;

        display: flex;
        justify-content: center;
        align-items: center;

        width: 22rem;
        height: 22rem;
        
        transform: translateX(55%);
        transition: .2s ease-in-out all;

        @include respond(tab-port) {
            width: 19rem;
            height: 19rem;

            transform: none;
        }
    }

    &__text {

        h4 {
            font-weight: 300;
            text-shadow: .2rem .2rem rgba(0, 0, 0, .2);
            border-bottom: .1rem solid rgba(0, 0, 0, .2);
            margin-bottom: .5rem;
        }

        justify-self: center;
        align-self: center;

        opacity: 0;

        transition: .2s ease-in-out all;
        transform: translateX(-50%);

        @include respond(tab-port) {
            opacity: 1;
            transform: none;
        }
    }

    &:hover .circle {
        transform: translateX(0%);
    }

    &:hover &__text {
        opacity: 1;
        transform: translateX(0%);
    }
}